.Business {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 16.66rem;
    margin: 0 0.5rem 2.3rem 0.5rem;
}

.Business .image-container {
    height: 16.66rem;
    margin-bottom: 5rem;
}

.Business h2 {
    margin-bottom: 0.5rem;
    font-size: 1.2rem;
    font-weight: 600;
    font-family: Arial, Helvetica, sans-serif;
}

.Business-Informaiton {
    display: flex;
    justify-content: space-between;
    border: 2px solid gold;
}

.Business-Informaiton p {
    font-size: 0.88rem;
    font-weight: 300;
    line-height: 1rem;
    font-family: Arial, Helvetica, sans-serif;
}

.Business-address {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.Business-reviews {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    text-align: right;
}

.Business-reviews h3 {
    color: #CCA353;
    font-weight: 600;
}

.Business-reviews .rating {
    font-size: .88rem;
    line-height: 1rem;
}